<template>
  <div id="myinfo">
    <!-- 导航条 -->
    <mt-header title="" fixed>
      <mt-button  icon="back" slot="left" @click="handleBack()">返回</mt-button>
      <!-- <mt-button  slot="right" @click="gotoPostAsk()">提问</mt-button> -->
    </mt-header>

    <!-- 用户信息 -->
    <div id="user">
      <div class="avatar">
        <img slot="icon" :src="getUserAvatar" width="70" height="70" class="userAvatar">
        <img v-if="userInfo.ifzj" class="icon" src="../../images/main/v_tag.png">
        <div class="edit">
          <CFWUpload
            flag="'thumbnail'"
            @complete="uploadComplete"
            @progress="uploadProgress" >
            <span>编辑</span>
          </CFWUpload>
        </div>
        <!-- <span class="edit"onloadedmetadata="">修改</span> -->
      </div>

      <div class="userInfo" v-if="!isEdit">
        <span @click="setEidt()">{{userInfo.trueName}}<img src="../../images/main/edit_icon.png" class="edit_icon"></span>
        <div class="label" v-if="userInfo.ifzj" ><i v-for="item in getLabel">{{item}}</i></div>
        <p>{{userInfo.signContent}}</p>
      </div>
      <div class="userInfo_edit" v-if="isEdit">
        <input v-model="userInfo.trueName" placeholder="输入你的名字"/>
        <input v-model="userInfo.tags" placeholder="输入你擅长多个以','隔开" v-if="userInfo.ifzj" />
        <textarea v-model="userInfo.signContent" placeholder="输入你的简介"></textarea>
        <em @click="upUserInfo()">保存</em>
      </div>
    </div>

    <!-- 10线条 -->
    <div class="lise_10"></div>

    <div id="userItems">
      <ul>
        <li @click="gotoAddress('publishAsk',true)">
          <div class="left"><img src="../../images/myinfo/publish_icon.png"><span>我要提问</span></div>
          <div class="right"><img src="../../images/main/Arrow_home.png"></div>
        </li>
        <li @click="gotoAddress('myAsk',true)">
          <div class="left"><img src="../../images/myinfo/myinfo_1.png"><span>我的提问</span></div>
          <div class="right"><img src="../../images/main/Arrow_home.png"></div>
        </li>
        <li @click="gotoAddress('myAnswer',true)" class="RLBorder">
          <div class="left"><img src="../../images/myinfo/myinfo_2.png"><span>我的回答</span></div>
          <div class="right"><img src="../../images/main/Arrow_home.png"></div>
        </li>
        <li @click="gotoAddress('myFollow',true)" class="badgeImg">
          <div class="left"><img src="../../images/myinfo/myinfo_3.png" ><span>我的关注</span></div>
          <div class="right"><img src="../../images/main/Arrow_home.png"></div>
        </li>
      </ul>
    </div>

    <div class="btnTop" v-if="isLogin">
      <mt-button  type="danger" size="large" @click="outSign()">退出账号</mt-button>
    </div>

  </div>
</template>

<script>
import {Toast,Indicator} from 'mint-ui'
import {getStore, setStore,removeStore,imgBaseUrl,upImgBaseUrl} from '../../components/common/mUtils'
import CFWUpload from '../../components/common/CFWUpload'
export default {
  data(){
    return{
      imgBaseUrl,
      upImgBaseUrl,
      isLogin:false,
      userID:null,
      userToken:null,
      userInfo:'', // 用户信息
      isEdit:false
    }
  },
  created(){
    this.userID = getStore('userID')
    this.userToken = getStore('userToken')
    if (this.userID && this.userToken) {
      this.isLogin = 1
    }else {
      this.$router.push('/login')
    }
  },
  mounted(){
    if (this.isLogin) {
      this.getData()
    }
  },
  components:{
    CFWUpload
  },
  computed:{
    getUserAvatar:function(){
      if(this.userInfo){
        let indexOf = this.userInfo.avatarPath.indexOf('http://tu.07358.com')
        if(indexOf != -1){
          return this.userInfo.avatarPath
        }else {
          return imgBaseUrl + this.userInfo.avatarPath
        }
      }else {
        return "http://qpic.07358.com/FiPMqTlrYzi_8naGgKIfj4h9wGLi"
      }
    },
    getLabel(){
      if (this.userInfo.tags !='') {
        let imags = this.userInfo.tags.split(',')
        return imags;
      }else {
        return []
      }
    }
  },
  methods: {
    handleBack(){
      this.$router.go(-1)
    },
    gotoAddress(path,login){
      if (login) {
        if (this.isLogin) {
          this.$router.push(path)
        }else {
          this.$router.push('/login')
        }
      }else {
          this.$router.push(path)
      }
    },
    setEidt(){
      this.isEdit = true
    },
    getData(){
      Indicator.open();
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/user?token='+this.userToken
      this.$http.post(getUrl).then(response => {
        if (response.data.code == 200) {
          this.userInfo = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('请求失败');
        Indicator.close();
        console.log(response);
      });
    },
    // 修改用户资料
    upUserInfo(){
      if (!this.isLogin) {
        Toast('请先登录！')
        return
      }
      Indicator.open('提交中...');
      let params ={
        trueName : this.userInfo.trueName, // 昵称
        signContent : this.userInfo.signContent, // 个性签名
        avatarPath : this.userInfo.avatarPath, // 头像
        tags : this.userInfo.tags, // 标签
      }

      let getUrl = this.CFWConfig.APIHost + '/v3/ask/upuser?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(params))
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          setTimeout(() => {
            this.isEdit = false
            this.getData()
          }, 800);
        }
        Toast(response.data.message);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    // 参数转化成表单对象
    objToformData(obj){
      // let params = new URLSearchParams()
      let params = new FormData()
      Object.keys(obj).forEach( (key, i) => {
        params.append(key, obj[key]);
      });
      return params;
    },
    // 上传图片过程中的回调
    uploadProgress (progress, flag) {
        //这里可以通过回调的flag对不同上传域做处理
        this.progress = progress < 100 ? progress : 0;
    },
    // 上传图片完成的回调
    uploadComplete(status, result, flag) {
        if (status == 200) { //
          let imgUrl = upImgBaseUrl + result.key //七牛域名 + 返回的key 组成文件url
          this.userInfo.avatarPath = result.key
          this.upUserInfo()
          //this.askParams.img.push(imgUrl)
        } else {
            //失败处理
        }
    },
    gotoTest(text){
      if (!text) {
        text = '提示信息'
      }
      Toast({
        message: text,
        duration: 800
      });
    },
    outSign(){
      removeStore('userID')
      removeStore('userToken')
      removeStore('userSF')
      this.isLogin = 0
      this.messageCount = 0
      this.showMsg('退出成功')
      this.$router.push('/')
    },
    showMsg(msg){
      Toast({
        message:msg,
        duration: 800
      });
    },
  }
}

</script>

<style>
/*引入公共样式*/
/*@import '../../style/common/style.css';*/

/*导航标题*/


#user{
  margin-top: 40px;
  /*background-color: #31a4fc;*/
  /*height: 135px;*/
  color: #000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#user .avatar{
  position: relative;
  width: 70px;
}

#user .avatar .userAvatar{
  margin-top: 20px;
  margin-bottom: 10px;
  border-radius: 35px;
}

#user .avatar .icon{
  position: absolute;
  bottom: 12px;
  right: 2px;
  width: 15px;
  height: 15px;
}

#user .avatar .edit{
  position: absolute;
  bottom: 18px;
  right: 18px;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
  padding: 2px;
  font-weight: bold;
}


/*****  用户资料*****/
#user .userInfo{
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items: center;
}

#user .userInfo span{
  text-align: left;
  line-height: 22px;
  color: #000;
  font-size: 15px;
  font-weight: bold;
}

#user .userInfo p{
  font-size: 14px;
  padding: 10px 20px;
}

#user .userInfo .label{
  display: flex;
  flex-direction: row;
  align-items: center;
}

#user .userInfo .label i{
  font-size: 10px;
  border: 1px solid #f4f7f9;
  color: #849aae;
  padding: 3px 8px;
  line-height: 14px;
  border-radius: 13px;
  margin-left: 10px;
  font-style: normal;
}

#user .userInfo span .edit_icon{
  width: 15px;
  height: 15px;
  margin-left: 10px;
}

/***** 编辑用户信息  ********/
#user .userInfo_edit{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 20px;

}

#user .userInfo_edit input,textarea{
  border: 1px solid #ddd;
  margin-bottom: 10px;
  padding: 3px;
  border-radius: 2px;
  font-size: 14px;
}

#user .userInfo_edit textarea{
  height: 60px;
}

#user .userInfo_edit em{
  background-color: #0089e4;
  padding: 8px 15px;
  color: #fff;
  font-style: normal;
  font-weight: bold;
}





/*用户功能区*/
#userItems ul{
    display: flex;
    flex-direction:column;
    padding: 0 8px;
}

#userItems ul li{
  border-bottom: 1px solid #ecebeb;
  height: 50px;
  display: flex;
  align-items:center;
  flex-direction:row;
  justify-content: flex-start;
}

#userItems ul li .left{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  flex-grow: 1;
}

#userItems ul li .left img{
  width: 23px;
  height: 23px;
  margin: 0 10px;
}

#userItems ul li .right img{
  width: 10px;
  height: 18px;
  margin: 0 10px;
}

.btnTop{
  padding: 20px;
}





</style>



