<template>
    <label class="mo-upload">
        <input type="file" :accept="accepts" @change="upload">
        <slot></slot>
    </label>
</template>
<style>
    .mo-upload {
        display: inline-block;
        position: relative;
        margin-bottom: 0;
    }

    .mo-upload input[type="file"] {
        display: none;
    }

    .mo-upload .mo-upload--label {
        display: inline-block;
        position: relative;
    }


</style>
<script>
  import lrz from 'lrz'
  import {getStore} from './mUtils'
  export default {
      name : 'MoUpload',
      props : {
          accepts : { //允许的上传类型
              type : String,
              default : 'image/jpeg,image/jpg,image/png,image/gif'
          },
          flag : [String, Number], //当前上传标识,以便于在同一个监听函数中区分不同的上传域
          maxSize : {
              type : Number,
              default : 0 //上传大小限制
          },
      },
      methods: {
          upload (event) {
              let file = event.target.files[0]
              let _this = this
              lrz(file,{width:800})
              .then(function (rst) {
                  // 处理成功会执行
                  _this.upQinin(rst.file)
              })
              .catch(function (err) {
                  // 处理失败会执行
              })
              .always(function () {
                  // 不管是成功失败，都会执行
              });
          },
          upQinin(file){
            const self = this
            const flag = this.flag
            if (file) {
                const formData = new FormData()
                formData.append('file', file)
                //获取token
                let geturl = this.CFWConfig.APIHost + '/v3/ask/qiniuusertoken'
                this.$http.get(geturl)
                .then(response => {
                    const result = response.body.result
                    formData.append('token', result)
                    // formData.append('key', 'cfw201705020001')
                    //提交给七牛处理
                    self.$http.post('http://up.qiniu.com', formData, {
                        progress(event) {
                            //传递给父组件的progress方法
                            self.$emit('progress', parseFloat(event.loaded / event.total * 100), flag)
                        }
                    })
                    .then(response => {
                        const result = response.body
                        if (result.hash && result.key) {
                            //传递给父组件的complete方法
                            self.$emit('complete', 200 , result, flag)
                            //让当前target可以重新选择
                            event.target.value = null
                        } else {
                            self.$emit('complete', 500, result, flag)
                        }
                    }, error => self.$emit('complete', 500, error.message), flag)
                })

            }

          }
      }
  }
</script>



